.register{
    
    width: 500px;
    height: auto;
    margin: auto;
    box-shadow: 2px 2px 2px 2px blue;
    padding: 20px;
    background-color: aquamarine;
    border-radius: 20px;
}

.card{
    box-shadow: 5px 5px 5px 5px lightgreen;
    padding: 20px;
}

@media screen and (max-width:750px) {
    .card{
       width: 750px;
    }
    
}

tbody{
    background-color: rgb(193, 255, 127);
}

thead,tbody{
    text-align: center;
}



.sort{
    display: flex;
    flex-wrap: wrap;
    
    
}

.button{
    flex-grow: 1;
}

.h{
    flex-basis: 150px;
    
}

.content{
    display: flex;
    column-gap: 10px;
    justify-content: center;
    
}

#top{
    display: flex;
    flex-direction: row;
    
}
#top #log{
    margin-left: 1030px;
}








